/*======================================
  Selectric v1.13.0
======================================*/

.selectric-wrapper {
  position: relative;
  cursor: pointer;
}

.selectric-responsive {
  width: 100%;
}

.selectric {
  border: 1px solid #abb5be;
  border-radius: 6px;
  background: #fff;
  position: relative;
  overflow: hidden;

  .selectric-below & {
    border-color: $blue;
  }

  &-form__field {
    margin-bottom: 22px;

    &.error {
      border-color: $red;

      &::placeholder {
        color: $red !important;
      }

      &.selectric-below {
        & .selectric {
          border-color: $blue;

          & .label {
            color: $blue !important;
          }
        }
      }
    }

    & .selectric {
      height: 52px;

      & .label {
        padding: 16px 26px;
        margin-left: 0;
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
}

.selectric .label {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 40px 0 1rem;
  font-size: 16px;
  line-height: 1;
  padding: 17px 0px;
  text-transform: uppercase;
  font-weight: 700;
  font-family: $ff-proxima-nova;

  color: #abb5be;
  height: 52px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  .selectric-below & {
    color: $blue;
  }
}

.selectric .button {
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  width: 52px;
  height: 52px;
  line-height: 52px;
  background-color: transparent;
  color: transparent;
  text-align: center;
}

.selectric .button:after {
  content: " ";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 15px;
  height: 10px;
  background: url("../img/arrow-down.svg") no-repeat 50% 50%;
}

.selectric-focus .selectric {
  // border-color: #aaaaaa;
}

.selectric-hover .selectric {
  // border-color: #c4c4c4;
}

.selectric-hover .selectric .button {
  // color: #a2a2a2;
}

.selectric-hover .selectric .button:after {
  // border-top-color: #a2a2a2;
}

.selectric-open {
  z-index: 9999;
}

.selectric-open .selectric {
  // border-color: #c4c4c4;
}

.selectric-open .selectric-items {
  display: block;
  z-index: 100;
}

.selectric-disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
}

.selectric-hide-select select {
  position: absolute;
  left: -100%;
}

.selectric-hide-select.selectric-is-native {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.selectric-hide-select.selectric-is-native select {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  z-index: 1;
  box-sizing: border-box;
  opacity: 0;
}

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  background: none !important;
}

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important;
}

/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: #f8f8f8;
  border: 1px solid #c4c4c4;
  z-index: -1;
  box-shadow: 0 0 10px -6px;
}

.selectric-items .selectric-scroll {
  height: 100%;
  overflow: auto;
}

.selectric-above .selectric-items {
  top: auto;
  bottom: 100%;
}

.selectric-items ul,
.selectric-items li {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 12px;
  line-height: 20px;
  min-height: 20px;
}

.selectric-items li {
  display: block;
  padding: 10px;
  color: #666;
  cursor: pointer;
}

.selectric-items li.selected {
  background: #e0e0e0;
  color: #444;
}

.selectric-items li.highlighted {
  background: #d0d0d0;
  color: #444;
}

.selectric-items li:hover {
  background: #d5d5d5;
  color: #444;
}

.selectric-items .disabled {
  filter: alpha(opacity=50);
  opacity: 0.5;
  cursor: default !important;
  background: none !important;
  color: #666 !important;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.selectric-items .selectric-group .selectric-group-label {
  font-weight: bold;
  padding-left: 10px;
  cursor: default;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background: none;
  color: #444;
}

.selectric-items .selectric-group.disabled li {
  filter: alpha(opacity=100);
  opacity: 1;
}

.selectric-items .selectric-group li {
  padding-left: 25px;
}
